body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: monospace;
}

.App-link {
  color: #61dafb;
}

.linkedinlink {
  color: rgb(235, 65, 130);
  text-decoration: none;
}
.linkedinlink:hover {
  color: rgb(152, 129, 235);
  text-decoration: none;
}
.mainbutton {
  font-family: monospace;
  font-weight: bold;
}
.imagecover {
  width: 160px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  object-position: center right;
}
.companylink {
  color: #ec679f;
  text-decoration: none;
}
.companylink:hover {
  color: rgb(152, 129, 235);
  text-decoration: none;
}

#loader {
  -webkit-animation: fadein 3s linear 1 normal forwards;
          animation: fadein 3s linear 1 normal forwards;
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
    background-color: white;
  }
  to {
    background-color: #282c34;
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
    background-color: white;
  }
  to {
    background-color: #282c34;
    opacity: 1;
  }
}

.typewriter h2 {
  color: #fff;
  font-family: monospace;
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: 0.15em solid orange; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: 0.15em; /* Adjust as needed */
  -webkit-animation: typing 4s steps(30, end), blink-caret 1s step-end infinite;
          animation: typing 4s steps(30, end), blink-caret 1s step-end infinite;
}

/* The typing effect */
@-webkit-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: orange;
  }
}
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: orange;
  }
}

